@import '../../../../scss/variable';
@import '../../../../scss/mixin.scss';

.accardion {
  min-width: 600px;
  width: 100%;
  border-radius: 5px;
  margin-top: 1rem;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);

  &_lesson {
    padding: 1rem;
    display: flex;
    align-items: center;
    border-radius: 7px;
    background: #f5f5f5;

    &_name {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      flex-grow: 1;
      @include font(500, 12px, 150%);
      margin-left: 0.75rem;
      letter-spacing: -0.05em;
      color: #9ca3af;
    }
  }

  &_control_btn {
    flex-shrink: 0;
    margin-left: 1.5rem;
    color: $font-title;
    transform: rotate(0) translateY(-2px);
    -webkit-transform: rotate(0) translateY(-2px);
    -moz-transform: rotate(0) translateY(-2px);
    -ms-transform: rotate(0) translateY(-2px);
    -o-transform: rotate(0) translateY(-2px);
    transition: rotate 0.2s ease-in;
    -webkit-transition: rotate 0.2s ease-in;
    -moz-transition: rotate 0.2s ease-in;
    -ms-transition: rotate 0.2s ease-in;
    -o-transition: rotate 0.2s ease-in;
    &.open {
      color: #9ca3af;
      transform: rotate(180deg) translateY(-2px);
      -webkit-transform: rotate(180deg) translateY(-2px);
      -moz-transform: rotate(180deg) translateY(-2px);
      -ms-transform: rotate(180deg) translateY(-2px);
      -o-transform: rotate(180deg) translateY(-2px);
      transition: rotate 0.2s ease-in;
      -webkit-transition: rotate 0.2s ease-in;
      -moz-transition: rotate 0.2s ease-in;
      -ms-transition: rotate 0.2s ease-in;
      -o-transition: rotate 0.2s ease-in;
    }
  }

  &_lessons_block {
    display: flex;
    flex-direction: column;
    margin-top: 0.75rem;
    gap: 0.625rem;
  }

  &_progress {
    display: flex;
    align-items: center;
    flex-shrink: 0;
    margin-left: auto;

    &_item {
      display: flex;
      align-items: center;
      margin-left: 3em;
      gap: 5px;
      & span {
        @include font(500, 12px, 150%);
        color: $font-title;
      }
    }
  }

  &_group {
    & span {
      @include font(400, 12px, 150%);
      letter-spacing: -0.03em;
      color: #717985;
    }
    display: flex;
    align-items: center;
    gap: 5px;
  }

  &_course_name {
    @include font(600, 12px, 150%);
    color: $font-title;
  }

  &_course_avatar {
    height: 2.5rem;
    width: 2.5rem;
    flex-shrink: 0;
    border-radius: 5px;
    background: #c7bce9;
  }
  &_course_img {
    height: 2.5rem;
    width: 2.5rem;
    object-fit: cover;
    max-width: 100%;
    flex-shrink: 0;
    border-radius: 5px;
  }

  &_info {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-left: 1rem;
  }

  &_item {
    margin-top: 1.25rem;
    &:first-child {
      margin-top: 0;
    }
    &_name {
      @include font(600, 12px, 150%);
      color: $font-title;
    }
    // overflow: hidden;
    // transition: height 0.3s ease;
  }

  &_header {
    padding: 1.25rem;
    cursor: pointer;
    display: flex;
    align-items: center;

    // &:hover {
    //   background-color: #e0e0e0;
    // }
  }

  &_content {
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    padding-bottom: 1.25rem;
    margin-top: 1.75rem;
    // height: 0;
    // opacity: 0;
    transition: height 0.3s ease, opacity 0.3s ease;
  }
}
