@import '../../scss/variable';
@import '../../scss/mixin.scss';

.navbar {
  @include position_t_l(fixed, 0, 0, 10);
  @include display(flex, column, flex-start, center);
  width: 80px;
  min-height: 90vh;
  height: 100%;
  background: #ffffff;
  box-shadow: 0 0 5px #d9d9d9;
  padding: 25px 0;

  &_menu {
    cursor: pointer;
  }

  &_settings {
    cursor: pointer;
  }

  &_setting_account {
    @include display(flex, column);
    cursor: pointer;
    margin-top: auto;

    & a {
      @include display(flex, row, center, center);
      width: 57px;
      height: 57px;
      text-align: center;
      color: #e0dced;

      &:hover {
        border-radius: 10px;
        background: #e0dced;
        color: #ba75ff;
      }
    }

    & a:not(:first-child) {
      margin-top: 30px;
    }
  }
}

.chatIcon {
  color: #e0dced;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 57px;
  height: 57px;
  text-align: center;
  margin-top: 30px;
  border-radius: 10px;

  &:hover {
    background: #e0dced;
    color: #ba75ff;
  }
}

.chatIcon_active {
  background: #e0dced;
  color: #ba75ff;
}

.isActive {
  @include display(flex, row, center, center);
  background: #e0dced;
  border-radius: 10px;
  text-align: center;
  color: #ba75ff !important;
}

.mobileIsActive {
  & svg {
    & path {
      //fill: $font-color-rose;
      stroke: $font-color-rose;
    }
  }
}
