@import '../../scss/mixin.scss';

.textField {
  margin-bottom: 40px;

  &_btnSave {
    @include display(flex, row, center);
    margin-top: 10px;
    width: 120px;
    height: 36px;
    border: none;
    text-align: center;
    background: #6b728f;
    font-size: 14px;
    color: #ffffff;
    border-radius: 6px;
    padding: 11px 0 11px;
    cursor: pointer;
  }

  &_btnCancel {
    @include display(flex, row, center);
    margin-top: 10px;
    width: 120px;
    height: 36px;
    border: none;
    text-align: center;
    background: #f3f4f6;
    font-size: 14px;
    color: #6b728f;
    border-radius: 6px;
    padding: 11px 0 11px;
    cursor: pointer;
  }

  &_textarea {
    width: 100%;
    height: 200px;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: vertical;
  }

  &_btnEditText {
    @include display(flex);

    &_setting {
      &:hover {
        background-color: rgba(196, 156, 210, 0.4);
      }

      @include display(flex, row, flex-start, center);
      margin-top: 10px;
      width: 100px;
      height: 36px;
      border: none;
      text-align: center;
      background: #f3f4f6;
      font-size: 14px;
      color: #6b728f;
      border-radius: 6px;
      padding: 11px 0 11px 16px;
    }
  }

  &_description {

    &_text {
      margin-bottom: 10px;
      font-weight: bold;
      font-size: 16px;
      font-family: "Lucida Grande", "Lucida Sans Unicode", Verdana, Helvetica, Arial, sans-serif;
    }
  }
}