@import '../../../scss/mixin.scss';

.wrapper {
  @include display(flex, row, center);
  padding: 2px 0 24px 0;
  margin-bottom: -13px;
}
.container {
  width: 100%;
  position: relative;
  color: #646f74;
  @include font(500, 11px);

  &_btn {
    position: relative;
    border: 1px solid #e5e7eb;
    background: #ffffff;
    border-radius: 5px;
    width: 100%;
    padding: 11px 0 11px 14px;
    text-align: left;
    color: #646f74;
    min-width: 100px;
    font-size: 16px;
  }

  & svg {
    @include position_t_r(absolute, 16px, 20px, 2);
  }

  & li {
    cursor: pointer;
    padding: 11px;
  }

  & li:hover {
    background: #f6f5f5;
  }
}
button:hover {
  cursor: pointer;
}
.options {
  @include position_t_l(absolute, 40px, 0, 30);
  border: 1px solid #e5e7eb;
  border-radius: 7px;
  width: 100%;
  background: #ffffff;
  display: none;
  list-style: none;
  margin-top: 10px;
  overflow: scroll;
  font-size: 16px;
  max-height: 300px;
}
.show {
  display: block;
  overflow: auto;
}
