@import '../../../scss/mixin.scss';

.custom_radio {
  @include position_t_l (absolute, 0, 0, -1);
  opacity: 0;
  z-index: 9999;
}
label {
  color: #4d5766;
  cursor: pointer;
}
.custom_radio + label {
  display: inline-flex;
  align-items: center;
  user-select: none;
}
.custom_radio + label::before {
  content: '';
  display: inline-block;
  width: 18px;
  height: 18px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #ba75ff;
  border-radius: 50%;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}
/*       */
.custom_radio:not(:disabled):not(:checked) + label:hover::before {
  border-color: #ba75ff;
}
/*     (   ) */
.custom_radio:not(:disabled):active + label::before {
  background-color: #b3d7ff;
  border-color: #b3d7ff;
}
/*   ,    */
.custom_radio:focus + label::before {
  //box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}
/*   ,         checked */
.custom_radio:focus:not(:checked) + label::before {
  border-color: #80bdff;
}
/*   ,    checked */
.custom_radio:checked + label::before {
  border-color: #ba75ff;
  background-color: #ba75ff;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%23fff'/%3e%3c/svg%3e");
}
/*   ,    disabled */
.custom_radio:disabled + label::before {
  background-color: #e9ecef;
}