@import '../../../scss/variable.scss';
@import '../../../scss/mixin.scss';    

.answerOptionsBlock {
    margin-top: 32px;
    &_title {
        @include font (500, 14px,17px);
        color: #4D5766;
    }
    &_inputWrapper {            
        @include display (flex, row, space-between, center);
        width: 100%;
        height: 36px;
        margin-top: 15px;
        label {
            margin: 0 18px 0 0;
            width: 60%;
            height: 100%;
            input {
                padding: 0 10px;

                &::placeholder {
                @include font (500, 14px, 21px);
                color: #D4D7DD;
                }
            }
        }
        &_correctAnswerWrapper {
            @include display (flex);
            height: 100%;
            border: 1px solid #E5E7EB;
            border-radius: 5px;
            margin: 0 9px 0 0;
            cursor: pointer;
            label {
                @include display (flex);
                margin: 0;
                padding: 9px;
                width: fit-content;
                @include font (500, 12px, 18px);
                color: #BABCBF;
                input {
                    cursor: pointer;
                }
                span {
                    width: 15px;
                    height: 15px;
                }
            }
        }
        &_comment {
            cursor: pointer;
            &:hover {
                svg {
                    path {
                        stroke: #BA75FF;
                    }
                    line {
                        stroke: #BA75FF;
                    }
                }
            }
        }
        &_delete {
            cursor: pointer;
            &:hover {
                svg {
                    path {
                        fill: #EF4444;
                    }
                }
            }
        }
        &_grab {            
            cursor: grab;
        }
    }
}